export class WindPlane extends THREE.Mesh {
    constructor(data) {
        super();
        this.geometry = new THREE.PlaneGeometry();
        this.geometry.translate(0, 0, -0.05);
        if (data) {
            this.setData(data);
        }
    }

    setData(data) {
        this.width = data.width;
        this.height = data.height;
        this.data = data.dst;
        if (this.material) {
            this.material.dispose();
        }
        this.material = new THREE.MeshBasicMaterial({
            map: this._createTexture(),
            transparent: true,
            opacity: 0.2,
        });
        return this;
    }

    _createTexture() {
        let size = this.width * this.height;
        let data = this.data;
        let img = new Uint8Array(size * 3);
        for (let j = 0; j < this.height; j++) {
            for (let i = 0; i < this.width; i++) {
                let index = j * this.width + i;
                let uv = new THREE.Vector2(data[0][index], data[1][index]);
                let c = Math.floor(uv.length() * 3);
                img[index * 3] = c;
                img[index * 3 + 1] = c;
                img[index * 3 + 2] = c;
                // img[index * 4 + 3] = 255;
            }
        }
        let texture = new THREE.DataTexture(
            img,
            this.width,
            this.height,
            THREE.RGBFormat
        );
        texture.minFilter = THREE.LinearFilter;
        texture.magFilter = THREE.LinearFilter;
        return texture;
    }
}

export default class WindParticles extends THREE.LineSegments {
    constructor(data) {
        super();
        this.material = new THREE.LineBasicMaterial({
            color: 0xffffff,
            vertexColors: true,
            // depthWrite: false,
            transparent: true,
            opacity: 0.3,
            blending: THREE.AdditiveBlending,
        });
        this.geometry = new THREE.BufferGeometry();
        this._clock = new THREE.Clock();
        this.anim = false;
        if (data) {
            this.setData(data);
        }
    }

    setData(data) {
        this.width = data.width;
        this.height = data.height;
        this.data = data.dst;
        this.reset();
        this.windPlane = new WindPlane(data);
        this.add(this.windPlane);
        return this;
    }

    //创建粒子
    _createParticles() {
        const particlesCount = this.width * this.height * 2;
        this.particles = [];
        for (let i = 0; i < particlesCount; i++) {
            this.particles[i] = new Particles();
        }
        return this;
    }

    //动画
    play(delta = 0.04) {
        const vertices = [],
            colors = [];
        for (let i = 0; i < this.particles.length; i++) {
            let part = this.particles[i];
            let uv = this._getUV(part.x, part.y);
            if (uv && Math.abs(uv.x) + Math.abs(uv.y) > 2 && part.life > 0) {
                //移动速度
                const speed = 0.003;
                //更新粒子位置,取得风杆坐标
                const line = part.next(uv.x, uv.y, delta * speed);
                //添加风杆两个顶点
                vertices.push(
                    line.x1 - 0.5,
                    line.y1 - 0.5,
                    0,
                    line.x2 - 0.5,
                    line.y2 - 0.5,
                    0
                );
                //添加两个顶点的颜色
                colors.push(1, 1, 1, 0.2, 0.2, 0.2);
                //生命值减一
                part.life--;
            } else {
                //重置粒子参数(随即位置)
                part.setData();
            }
        }
        this.geometry.setAttribute(
            "position",
            new THREE.Float32BufferAttribute(vertices, 3)
        );
        this.geometry.setAttribute(
            "color",
            new THREE.Uint8BufferAttribute(colors, 3)
        );
        return this;
    }

    update() {
        const delta = this._clock.getDelta();
        if (this.anim && this.visible) {
            this.play(delta);
        }
        return this;
    }

    reset() {
        this._createParticles();
        this.play();
        return this;
    }

    _getUV(x, y) {
        if (x < 0 || x > 1 || y < 0 || y > 1) {
            return null;
        }

        const xi = Math.floor(x * this.width),
            yi = Math.floor(y * this.height);
        const dx = x * this.width - xi,
            dy = y * this.height - yi;

        let data = this.data[0];
        //左上角索引
        const index = yi * this.width + xi;
        const u1 = data[index],
            u2 = data[index + 1],
            u3 = data[index + this.width + 1],
            u4 = data[index + this.width];
        const u = this._inter(u1, u2, u3, u4, dx, dy);

        data = this.data[1];
        const v1 = data[index],
            v2 = data[index + 1],
            v3 = data[index + this.width + 1],
            v4 = data[index + this.width];
        const v = this._inter(v1, v2, v3, v4, dx, dy);

        return new THREE.Vector2(u, v);
    }

    //双线性插值
    _inter(p1, p2, p3, p4, dx, dy) {
        let q1 = p1 + (p2 - p1) * dx;
        let q2 = p4 + (p3 - p4) * dx;
        return q1 + (q2 - q1) * dy;
    }
}

class Particles {
    constructor(x, y) {
        this.setData(x, y);
    }

    setData(x, y) {
        this.x = x || Math.random();
        this.y = y || Math.random();
        this.life = 100 + Math.floor(Math.random() * 100);
        return this;
    }

    next(u, v, speed = 0.1) {
        this.x += u * speed;
        this.y += v * speed;
        const dx = u * Particles.lineLen,
            dy = v * Particles.lineLen;
        const x1 = clamp(this.x + dx),
            y1 = clamp(this.y + dy),
            x2 = clamp(this.x - dx),
            y2 = clamp(this.y - dy);

        return { x1, y1, x2, y2 };

        function clamp(value) {
            if (value < 0) return 0;
            if (value > 1) return 1;
            return value;
        }
    }
}
Particles.lineLen = 0.0003;
