import PointVolumeMesh from "./pointVolumeMesh.js";
import VolumeClip from "./volumeClip.js";
import loader from "./volumLoader.js";

export default class Volume extends THREE.Object3D {
    constructor(data, w, h, d) {
        super();
        this.volume = new PointVolumeMesh(data, w, h, d);
        this.clip = new VolumeClip(this.volume);

        this.add(this.volume);
        this.add(this.clip._clipGroup);
    }

    setData(data, w, h, d) {
        this.volume.setData(data, w, h, d);
        this.clip.update();
    }

    async load(url) {
        const data = await loader(url);
        this.setData(data.dst, data.width, data.height, data.depth);
        return this;
    }
}
