//3D场景初始化类
export default class View {
    constructor(id) {
        if (id) {
            this.init(id);
        }
    }

    init(id) {
        //容器
        this.container = document.getElementById(id);
        //场景
        this.scene = new THREE.Scene();
        //渲染器
        this.renderer = new THREE.WebGLRenderer({
            // antialias: true,
        });

        //开启实时阴影太耗资源
        //this.renderer.shadowMap.enabled = true;
        this.renderer.localClippingEnabled = true;
        this.renderer.sortObjects = true;
        this.renderer.setClearColor(0xffffff, 0.0);
        this.renderer.setPixelRatio(window.devicePixelRatio);
        this.container.appendChild(this.renderer.domElement);
        //摄像机
        this.createCamera();
        //加入坐标轴
        this.addAxis();
        //加入灯光
        this.addLight();
        //鼠标控制
        this._createControl();
        //性能指示器
        this._createStates();
        //时钟
        this._clock = new THREE.Clock();
        //动画循环
        this.animate();
        //挂接resize
        this.resize();
        window.addEventListener("resize", this.resize.bind(this));
        return this;
    }

    resize() {
        this.width = this.container.clientWidth;
        this.height = this.container.clientHeight;
        this.renderer.setSize(this.width, this.height);
        this.camera.aspect = this.width / this.height;
        this.camera.updateProjectionMatrix();
        return this;
    }

    reset() {
        this.controls.reset();
        return this;
    }
    viewUp() {
        this.reset();
        this.camera.position.set(0, 0, 200);
        return this;
    }
    viewDown() {
        this.reset();
        this.camera.position.set(0, 0, -200);
        return this;
    }
    viewEast() {
        this.reset();
        this.camera.position.set(200, 0, 100);
        //this.camera.up.set(-1, 0, 0);
    }
    viewWest() {
        this.reset();
        this.camera.position.set(-200, 0, 100);
        this.camera.up.set(1, 0, 0);
        return this;
    }
    viewNorth() {
        this.reset();
        this.camera.position.set(0, 150, 100);
        this.camera.up.set(0, -1, 0);
        return this;
    }
    viewSouth() {
        this.reset();
        this.camera.position.set(0, -150, 30);
        return this;
    }

    add(obj) {
        this.scene.add(obj);
        return this;
    }

    remove(obj) {
        this.scene.remove(obj);
        return this;
    }

    get(name) {
        return this.scene.getObjectByName(name);
    }

    _createStates() {
        this.stats = new Stats();
        this.container.appendChild(this.stats.domElement);
        return this;
    }

    _createControl() {
        this.controls = new THREE.TrackballControls(
            this.camera,
            this.renderer.domElement
        );

        this.controls.minDistance = 5;
        this.controls.maxDistance = 5000;

        // this.controls = new THREE.PointerLockControls(
        //     this.camera,
        //     this.renderer.domElement
        // )

        // this.controls.minAzimuthAngle = 0.2;
        // this.controls.maxAzimuthAngle = 0.2;
        // this.controls.enableRotate = false;

        return this;
    }

    createCamera() {
        console.log("create camera");
        let w = this.container.clientWidth;
        let h = this.container.clientHeight;
        let VIEW_ANGLE = 45;
        this.camera = new THREE.PerspectiveCamera(
            VIEW_ANGLE,
            w / h,
            0.1,
            1000000
        );
        //this.camera = new THREE.OrthographicCamera(-w, w, h, -h, 0.1, 50000);
        this.camera.position.set(-10, -160, 90);
        this.add(this.camera);
        return this;
    }

    //添加光源
    addLight() {
        console.log("add light");
        // let pos = new THREE.Vector3(50, -50, 100);
        let pos = new THREE.Vector3(-150, 100, 100);
        // 添加点光源
        let pLight = new THREE.PointLight(0x555555);
        pLight.position.copy(pos);
        this.add(pLight);
        //光晕，表示太阳
        console.log("add sun");
        let lensflare = new THREE.Lensflare();
        lensflare.position.copy(pos);
        let flareColor = new THREE.Color(0x666666);
        let loader = new THREE.TextureLoader();
        let textureFlare0 = loader.load("./image/lensflare0.png");
        let textureFlare3 = loader.load("./image/lensflare3.png");
        lensflare.addElement(
            new THREE.LensflareElement(textureFlare0, 700, 0, flareColor)
        );
        lensflare.addElement(
            new THREE.LensflareElement(textureFlare3, 60, 0.6)
        );
        lensflare.addElement(
            new THREE.LensflareElement(textureFlare3, 70, 0.7)
        );
        lensflare.addElement(
            new THREE.LensflareElement(textureFlare3, 120, 0.9)
        );
        lensflare.addElement(new THREE.LensflareElement(textureFlare3, 70, 1));
        this.sun = lensflare;
        this.add(lensflare);
        // 添加平行光源
        let light = new THREE.DirectionalLight(0xddddddd);
        light.position.set(pos);
        light.castShadow = true;
        this.add(light);
        this.light = light;
        //添加漫反射
        let aLight = new THREE.AmbientLight(0x999999);
        this.add(aLight);

        console.log("add back light");
        pLight = new THREE.PointLight(0x333333);
        // pLight.position.copy(new THREE.Vector3(-200, -100, -200));
        pLight.position.copy(new THREE.Vector3(-20, -10, 20));
        this.add(pLight);

        return this;
    }

    //添加坐标轴
    addAxis() {
        console.log("add Axis");
        let axis = new THREE.AxesHelper(100);
        axis.name = "axis";
        axis.visible = false;
        this.add(axis);
        return this;
    }

    animate() {
        // let delta = this._clock.getElapsedTime()
        this._clock.start();

        this.camera.lookAt(0, 0, 0);

        this.controls.update();
        this.stats.update();
        this.renderer.render(this.scene, this.camera);
        // this._clock.stop();
        this.update(this._clock.getElapsedTime());
        requestAnimationFrame(this.animate.bind(this));
    }

    set axisVisible(value) {
        this.scene.getObjectByName("axis").visible = value;
        return this;
    }

    get axisVisible() {
        return this.scene.getObjectByName("axis").visible;
    }

    update() {}
}
