import * as dat from "dat.gui";

let gui = new dat.GUI();
export default function createGui(params) {
    addViewOptions(params.view);
    addCloudOptions(params.pm25).open();
    addWindOptions(params.wind);
    addHighOptions(params.high);
    return gui;
}

function addViewOptions(view) {
    let folder = gui.addFolder("视角");
    if (view) {
        folder.add(view, "reset").name("复位");
        folder.add(view, "viewUp").name("顶视图");
        folder.add(view, "viewDown").name("底视图");
        folder.add(view, "viewEast").name("东视图");
        folder.add(view, "viewWest").name("西视图");
        folder.add(view, "viewNorth").name("北视图");
        folder.add(view, "viewSouth").name("南视图");
        folder.add(view, "axisVisible").name("坐标轴");
    }
    return folder;
}

function addCloudOptions(cloud) {
    let folder = gui.addFolder("PM2.5");
    if (cloud) {
        let count = 73;
        let vm = {
            setColor(c1, c2) {
                cloud.volume.color1 = c1;
                cloud.volume.color2 = c2;
            },
            aqiModel() {
                this.setColor(0x00ff00, 0xff0000);
            },
            fireModel() {
                this.setColor(0xff0000, 0x00ff00);
            },
            streamModel() {
                this.setColor(0x0000ff, 0xa00000);
            },
            cloudModel() {
                this.setColor(0xffffff, 0x000000);
            },
            smokeModel() {
                this.setColor(0x000000, 0x333333);
            },

            dataIndex: 0,
            anim: false,
            isLoading: false,

            get index() {
                return this.dataIndex;
            },

            set index(value) {
                if (value < 0 || value >= count) {
                    this.dataIndex = 0;
                    return;
                }
                this.dataIndex = value;
                let fn = Math.floor(value) + "";
                fn = `./data/pm25/${fn.padStart(2, "0")}.json`;
                console.log(fn);
                this.isLoading = true;
                cloud.load(fn).then(() => {
                    this.isLoading = false;
                });
            },

            next() {
                this.index++;
            },
        };

        let delay = 0;
        setInterval(() => {
            if (vm.anim && !vm.isLoading && cloud.volume.visible) {
                if (delay === 0) {
                    vm.index++;
                    if (vm.index === count - 1) {
                        delay = 20;
                    }
                } else {
                    delay--;
                }
            }
        }, 100);

        folder.add(cloud.volume, "visible").name("显示");
        folder.add(cloud.volume, "threshold", 0, 100, 1).name("阈值");
        folder.add(cloud.volume, "blendAdd").name("色彩增强");
        folder.addColor(cloud.volume, "color1").name("开始颜色").listen();
        folder.addColor(cloud.volume, "color2").name("结束颜色").listen();

        folder.add(vm, "aqiModel").name("AQI模式");
        folder.add(vm, "fireModel").name("火焰模式");
        folder.add(vm, "streamModel").name("虚幻模式");
        folder.add(vm, "cloudModel").name("白云模式");
        folder.add(vm, "smokeModel").name("烟雾模式");
        folder.add(vm, "next").name("单步动画");
        folder.add(vm, "anim").name("动画");
        folder.add(vm, "index", 0, 72, 1).name("时间轴").listen();

        let clipfolder = folder.addFolder("切片");
        clipfolder.close();
        let clip = cloud.clip;
        if (clip) {
            clipfolder.add(clip, "enable").name("显示");
            clipfolder.add(clip, "x", -0.5, 0.5, 0.01).name("x切片").listen();
            clipfolder.add(clip, "y", -0.5, 0.5, 0.01).name("y切片").listen();
            clipfolder.add(clip, "z", -0.5, 0.5, 0.01).name("z切片").listen();
            clipfolder.add(clip, "opacity", 0, 1, 0.1).name("切片透明度");
            // clipfolder.add(clip, "volumeVisible").name("显示体数据");
            clipfolder.add(clip, "smooth").name("切面纹理平滑");
        }
    }
    return folder;
}

function addWindOptions(wind) {
    let folder = gui.addFolder("风");
    if (wind) {
        folder.add(wind, "visible").name("显示");
        folder.add(wind, "anim").name("动画");
        folder.add(wind, "play").name("单步动画");
        folder.add(wind, "reset").name("复位");
        folder.add(wind.windPlane, "visible").name("风速背景");
    }
    return folder;
}

function addHighOptions(high) {
    let folder = gui.addFolder("高度");
    if (high) {
        folder.add(high, "visible").name("显示");
        folder.add(high, "wireframe").name("网格");
        folder.add(high, "hscale", 1, 10, 1).name("高度拉伸");
    }
    return folder;
}
