export default class HighMesh extends THREE.Mesh {
    constructor(data) {
        super();
        this._hscale = 5;

        if (data) {
            this.setData(data);
        }
    }

    setData(data) {
        this.width = data.width;
        this.height = data.height;
        this.data = data.dst;
        this.max = Math.max(...data.dst);
        this.min = Math.min(...data.dst);
        console.log(this.max, this.min);
        this.geometry = new THREE.PlaneBufferGeometry(
            1,
            1,
            this.width - 1,
            this.height - 1
        );
        this.material = new THREE.MeshPhongMaterial({
            transparent: true,
            opacity: 0.8,
            // depthTest: false,
            depthWrite: false,
            // vertexColors: THREE.VertexColors,
            side: THREE.DoubleSide,
            color: 0x002255,
            // color: 0xffffff,
            shininess: 30,
            specular: 0x555555,
            // wireframe: true,
        });
        const vertices = this.geometry.attributes.position.array;
        const colors = [];
        this.hscale = this._hscale;
        return this;
    }

    get hscale() {
        return this._hscale;
    }

    set hscale(value) {
        this._hscale = value;
        const vertices = this.geometry.attributes.position.array;
        const colors = [];
        for (let i = 0, j = 0, l = vertices.length; i < l; i++, j += 3) {
            const v = (this.data[i] - this.min) / (this.max - this.min);
            vertices[j + 2] = (v * value) / 10 - 0.5; // ((this.data[i] - this.min) / (this.max - this.min)) * value - 0.5;
        }
        this.geometry.computeVertexNormals();
        this.geometry.attributes.position.needsUpdate = true;
    }

    get wireframe() {
        return this.material.wireframe;
    }

    set wireframe(value) {
        this.material.wireframe = value;
        let color = value ? 0xaaaaaa : 0x002255;
        this.material.color.set(color);
    }
}
