/*体数据Mesh类
  根据三维数据生成体数据的Mesh，适合在三维空间显示数据的分布情况。
  TODO: 数据调整为按json格式存储（为减少数据量加快速度，可考虑使用图像存储数据）
*/
import { PointVolumeMaterial } from "./pointVolumeShader.js";

/**
 * 点状体数据Mesh
 *
 * @export PointVolumMesh类
 * @class PointVolumMesh
 * @extends {THREE.Points}
 */
export default class PointVolumeMesh extends THREE.Points {
    /**
     * @param {*} data 要分析的数据数组（三维数组的展开形式）
     * @param {*} width 数组宽度数（第一维度大小）
     * @param {*} height 数据高度数（第二维度大小）
     * @param {*} depth 数据的（第三维度大小）
     * @memberof PointVolumMesh
     */
    constructor(data, width, height, depth) {
        super();
        this.name = "cloud";
        // this.material = new THREE.ShaderMaterial(
        //     shader({
        //         color1: new THREE.Color("#00a000"),
        //         color2: new THREE.Color("#ff0000"),
        //         threshold: 0.01,
        //         map: new THREE.TextureLoader().load("../image/dot.png"),
        //     })
        // );

        // this.material = volumeMaterial({
        //     color1: new THREE.Color("#00a000"),
        //     color2: new THREE.Color("#ff0000"),
        //     threshold: 0.01,
        //     map: new THREE.TextureLoader().load("../image/dot.png"),
        // });

        // this.material = new THREE.PointsMaterial({
        //     size: 5,
        //     opacity: 0.5,
        //     transparent: true,
        //     color: 0x111111,
        //     // color: 0xffffff,
        //     depthWrite: false,
        //     map: new THREE.TextureLoader().load("../image/dot.png"),
        //     blending: THREE.AdditiveBlending,
        // });

        this.material = PointVolumeMaterial({
            color1: new THREE.Color("#00ff00"),
            color2: new THREE.Color("#ff0000"),
            threshold: 0.01,
            map: new THREE.TextureLoader().load("./image/dot.png"),
            clipx: 0.5,
            clipy: 0.5,
            clipz: 0.5,
        });

        if (data) {
            this.setData(data, width, height, depth);
        }
    }

    /**
     * 加载数据
     * @param {*} data 要分析的数据数组（三维数组的展开形式）
     * @param {*} width 数组宽度数（第一维度大小）
     * @param {*} height 数据高度数（第二维度大小）
     * @param {*} depth 数据的（第三维度大小）
     * @memberof PointVolumMesh
     */
    setData(data, width, height, depth) {
        this.data = data;
        if (this.geometry) {
            this.geometry.dispose();
        }
        this.depth = depth;
        this.width = width;
        this.height = height;
        if (this.geometry) {
            this.geometry.dispose();
        }
        this.geometry = this._createPoints();
        this.geometry.computeBoundingBox();
        return this;
    }

    /**
     * 根据data生成Mesh
     * @return {*} geometry
     * @memberof PointVolumMesh
     */
    _createPoints() {
        const data = this.data;
        // 生成顶点数据
        let index = 0,
            positions = [],
            values = [];
        this.max = -Infinity;
        this.min = Infinity;
        for (let k = 0; k < this.depth; k++) {
            for (let j = 0; j < this.height; j++) {
                for (let i = 0; i < this.width; i++) {
                    //值
                    let value = +data[index] / 100;
                    //最大最小值
                    this.max = Math.max(value, this.max);
                    this.min = Math.min(value, this.min);
                    if (value < 0) value = 0;
                    if (value > 1) value = 1;
                    //只添加>0的顶点
                    if (value > 0.01) {
                        positions.push(i, j, k);
                        values.push(value);
                    }
                    index++;
                }
            }
        }
        console.log(
            `geomentry:${positions.length} vertices\n max:${this.max.toFixed(
                3
            )},min:${this.min.toFixed(3)}`
        );

        //创建geometry，将数据写入顶点数组
        let geometry = new THREE.BufferGeometry();
        geometry.setAttribute(
            "position",
            new THREE.Float32BufferAttribute(positions, 3)
        );
        geometry.setAttribute(
            "value",
            new THREE.Float32BufferAttribute(values, 1)
        );
        //缩放到0-1范围
        geometry.scale(1 / this.width, 1 / this.height, 1 / this.depth);
        //平移到（0，0）
        geometry.translate(-0.5, -0.5, -0.5);

        return geometry;
    }

    /**
     *
     * 设置mesh的开始颜色
     * @memberof PointVolumMesh
     */
    set color1(value) {
        this.material.uniforms.color1.value.set(value);
        return this;
    }

    /**
     *
     * 取得mesh的开始颜色
     * @memberof PointVolumMesh
     */
    get color1() {
        return this.material.uniforms.color1.value.getHex();
    }

    /**
     *
     * 设置mesh的结束颜色
     * @memberof PointVolumMesh
     */
    set color2(value) {
        this.material.uniforms.color2.value.set(value);
        return this;
    }

    /**
     *
     * 取得mesh的结束颜色
     * @memberof PointVolumMesh
     */
    get color2() {
        return this.material.uniforms.color2.value.getHex();
        // return "#" + color.getHexString();
    }

    /**
     *
     * 设置mesh的阈值（点的值>threshold才显示）
     * @memberof PointVolumMesh
     */
    set threshold(value) {
        this.material.uniforms.threshold.value = value / 100.0;
        return this;
    }

    /**
     *
     * 取得mesh的阈值
     * @memberof PointVolumMesh
     */
    get threshold() {
        return this.material.uniforms.threshold.value * 100.0;
    }

    /**
     * 颜色增强
     * @memberof PointVolumMesh
     */
    set blendAdd(value) {
        this.material.blending = value
            ? THREE.AdditiveBlending
            : THREE.NormalBlending;
    }

    get blendAdd() {
        return this.material.blending === THREE.AdditiveBlending;
    }
}

