export default async function (url) {
    console.log("downloading data...");
    let loader = new THREE.FileLoader();
    loader.setResponseType("text");
    return new Promise((resolve, reject) => {
        loader.load(
            url,
            //scucess
            function (data) {
                resolve(parse(data));
            },
            // progress
            function (xhr) {
                let prog = xhr.loaded / xhr.total;
                console.log(`downloading...${(prog * 100).toFixed(0)}%`);
            },
            //error
            function (err) {
                console.error("An error happened:" + err);
                reject(err);
            }
        );
    });
}

function parse(txt) {
    //var gridData = [];
    var head = {};
    var i, j;
    var values = txt.split(/[\s+\n+]+/);
    var fields = [
        "diamond",
        "type",
        "caption",
        "year",
        "month",
        "day",
        "hour",
        "span",
        "level",
        "lonSpan",
        "latSpan",
        "beginLon",
        "endLon",
        "beginLat",
        "endLat",
        "countLon",
        "countLat",
        "levelSpan",
        "levelBegin",
        "levelEnd",
        "smoothingCoefficient",
        "boldValue",
    ];
    for (i in fields) {
        if (i > 2) values[i] -= 0;
        head[fields[i]] = values[i];
    }
    if (head.diamond != "diamond" || head.type != "4") {
        throw "文件类型不正确";
    }

    var levels = [];
    for (i = head.levelBegin; i <= head.levelEnd; i += head.levelSpan) {
        levels.push(i);
    }

    var index = 22;

    return {
        width: head.countLon,
        height: head.countLat,
        levels: levels,
        dst: values.slice(index, index + head.countLon * head.countLat),
    };
}
