export default class Axis extends THREE.Mesh {
    constructor(levels) {
        super();
        const len = levels.length;
        const color = 0x777777;
        const material = new THREE.LineBasicMaterial({ color });
        for (let i in levels) {
            const level = levels[i];
            const points = [];
            const h = i / (len - 1) - 0.5;
            points.push(new THREE.Vector3(-0.5, -0.5, h));
            points.push(new THREE.Vector3(-0.5, 0.5, h));
            points.push(new THREE.Vector3(0.5, 0.5, h));
            points.push(new THREE.Vector3(0.5, -0.5, h));
            const geo = new THREE.BufferGeometry().setFromPoints(points);
            this.add(new THREE.Line(geo, material));
        }

        const box = new THREE.BoxHelper(
            new THREE.Mesh(new THREE.BoxGeometry()),
            color
        );
        this.add(box);

        const grid = new THREE.GridHelper(1, 10, 0x444444, 0x333333);
        grid.rotateX(0.5 * Math.PI);
        grid.translateY(-0.47);
        this.add(grid);

        const _this = this;
        let loader = new THREE.FontLoader();
        loader.load("./lib/font.json", function (font) {
            for (let i = 0; i < len; i += 2) {
                console.log(i);
                const level = levels[i];
                const h = i / (len - 1) - 0.5;
                let shapes = font.generateShapes(
                    (level * 100 + "").padStart(4, " "),
                    0.008
                );
                let text1 = new THREE.Mesh(
                    new THREE.ShapeBufferGeometry(shapes),
                    new THREE.LineBasicMaterial({ color: 0x888888 })
                );
                text1.position.set(-0.53, -0.5, h);
                let text2 = text1.clone();
                text2.position.set(0.502, -0.5, h);
                _this.add(text1, text2);
            }
        });
    }
}
