export default async function (url) {
    console.log("downloading data...");
    let loader = new THREE.FileLoader();
    // loader.setResponseType("text");
    return new Promise((resolve, reject) => {
        loader.load(
            url,
            //scucess
            function (data) {
                resolve(parse(data));
            },
            // progress
            function (xhr) {
                let prog = xhr.loaded / xhr.total;
                console.log(`downloading...${(prog * 100).toFixed(0)}%`);
            },
            //error
            function (err) {
                console.error("An error happened:" + err);
                reject(err);
            }
        );
    });
}

function parse(txt) {
    var head = {};
    var i, j;
    var values = txt.split(/[\s+\n+]+/);

    var fields = [
        "diamond",
        "type",
        "caption",
        "year",
        "month",
        "day",
        "hour",
        "span",
        "level",
        "lonSpan",
        "latSpan",
        "beginLon",
        "endLon",
        "beginLat",
        "endLat",
        "countLon",
        "countLat",
    ];
    for (i in fields) {
        if (i > 2) values[i] -= 0;
        head[fields[i]] = values[i];
    }
    if (head.diamond != "diamond" || head.type != "11") {
        throw "文件类型不正确";
    }

    let u = [],
        v = [];
    let index = fields.length;
    let size = head.countLon * head.countLat;
    for (let j = 0; j < head.countLat; j++) {
        for (let i = 0; i < head.countLon; i++) {
            index = j * head.countLon + i + fields.length;
            u.push(+values[index]);
            v.push(+values[index + size]);
        }
    }
    return {
        width: head.countLon,
        height: head.countLat,
        dst: [u, v],
    };
}
