/**
 *点状体数据材质
 *
 * @export
 * @param {*} options
 * @return {*}
 */
export function PointVolumeMaterial(options) {
    // let uniforms = THREE.UniformsUtils.merge([
    //     {
    //         color1: { value: options.color1 },
    //         color2: { value: options.color2 },
    //         threshold: { value: options.threshold },
    //         map: { value: options.map },
    //     },
    // ]);

    let uniforms = {
        color1: { value: options.color1 },
        color2: { value: options.color2 },
        threshold: { value: options.threshold },
        map: { value: options.map },
        clipx: { value: options.clipx },
        clipy: { value: options.clipy },
        clipz: { value: options.clipz },
    };

    //顶点着色器
    let vertexShader = `
        attribute float value;
        uniform vec3 color1;
        uniform vec3 color2;
        uniform float threshold;

        varying vec4 vColor;
        varying vec3 vposition;
        varying float vclipx;


        uniform float clipx;
        uniform float clipy;
        uniform float clipz;


        void main() {

            vposition = position;
            vColor = vec4(mix(color1,color2,value),value/4.0);
            vclipx = clipx;

            if(value<threshold||position.x>clipx||position.y<-clipy||position.z>clipz){
                vColor = vec4(0.0);
            }

            vec4 mvPosition = modelViewMatrix * vec4( position, 1.0 );
            gl_PointSize = (11.0) * (300.0 / -mvPosition.z);
            //gl_PointSize = (value) * (500.0 / -mvPosition.z);
            gl_Position = projectionMatrix * mvPosition;         
            
        }
    `;
    //片元着色器
    let fragmentShader = `
        uniform sampler2D map;

        varying vec4 vColor;
        varying vec3 vposition;
        varying float vclipx;

        
        void main() {

            gl_FragColor = vColor;
            gl_FragColor = gl_FragColor * texture2D( map, gl_PointCoord);
        }
    `;

    let material = new THREE.ShaderMaterial({
        uniforms,
        //顶点着色器
        vertexShader,
        //片元着色器
        fragmentShader,
        blending: THREE.AdditiveBlending,
        depthTest: true,
        depthWrite: false,
        transparent: true,
        clipping: true,
    });
    // material.setValues(options);

    return material;
}

